body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Roboto", "Segoe UI",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

.App-header {
    background-color: #f3f3f3;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

a {
    text-decoration: none;
    color: white;
}

.nav-bar {
    background-color: #d8001f;
    display: flex;
    justify-content: space-between;
}

.nav-bar ul {
    list-style: none;
    margin: 0;
}

.nav-bar li {
    display: inline-block;
}

.login {
    position: fixed;
    right: 20px;
    transition: all 0.25s;
    border-radius: 0.5rem;
    background-color: white;
    box-shadow: 2px 10px 10px 0.3px #868686;
    padding: 0.5rem;
    z-index: 10;
}

.login-open {
    top: 48px;
}

.login-closed {
    visibility: collapse;
    top: -50px;
    opacity: 0;
}

.logo-normal {
    border-radius: 50%;
    width: 50px;
    height: 50px;
    object-fit: cover;
}

.logo-large {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-left: 2rem;
}

.product-header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.product {
    box-shadow: 1px 2px 20px 1px#d4d4d4;
    text-align: left;
    margin: 25px 25px;
    width: 100%;
    padding: 30px;
    border-radius: 25px;
    background-color: white;
}

.products {
    display: flex;
    min-height: 90vh;
    background-color: #fafafa;
}

.sidebar {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
}

ul {
    list-style: none;
}

.product-info-list {
    display: flex;
    justify-content: space-around;
    border: 1px solid black;
    max-width: 85%;
    margin: auto;
}

.create-product-header {
    font-family: "Roboto";
    font-weight: 300;
    font-style: italic;
    margin: 20px auto;
    position: relative;
    text-align: center;
}

